// pages / dashboard / account / manage

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class AccountManage extends React.Component {
    componentDidMount() {
        $('.account-manage-table').DataTable({
            pageLength: 10,
            responsive: true,
            dom: '<"html5buttons"B>lTfgitp',
            buttons: [
                {
                    extend: 'copy'
                },
                {
                    extend: 'csv'
                },
                {
                    extend: 'excel',
                    title: 'ExampleFile'
                },
                {
                    extend: 'pdf',
                    title: 'ExampleFile'
                },

                {
                    extend: 'print',
                    customize: function(win) {
                        $(win.document.body).addClass('white-bg');
                        $(win.document.body).css('font-size', '10px');

                        $(win.document.body).find('table')
                            .addClass('compact')
                            .css('font-size', 'inherit');
                    }
                }
            ]

        });
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Account - manage" meta={[
                        {
                            name: 'description',
                            content: 'Account - manage'
                        }, {
                            name: 'keywords',
                            content: 'Account'
                        }
                    ]}/>
                    {/* End of Helmet */}

                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-10">
                            <h2>Account Management</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Account Mgmt</strong>
                                </li>
                            </ol>
                        </div>
                        <div className="col-lg-2">

                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInRight">
                        <div className="row">
                            <div className="col-lg-4">
                                <div className="ibox">
                                    <div className="ibox-title">
                                        <h5>Accounts Package Info</h5>
                                    </div>
                                    <div className="ibox-content">
                                        <div className="team-members">
                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a1.jpg" /></a>
                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a2.jpg" /></a>
                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a3.jpg" /></a>
                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a5.jpg" /></a>
                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a6.jpg" /></a>
                                            <a href="#">
                                                <div style={{
                                                    display: 'inline-block',
                                                    background: 'rgba(0,0,0,.5)',
                                                    width: '42px',
                                                    height: '42px',
                                                    verticalAlign: 'middle',
                                                    borderRadius: '50%',
                                                    lineHeight: '42px',
                                                    fontWeight: 'bold',
                                                    color: '#fff',
                                                    textAlign: 'center'
                                                }}>
                                                  133+
                                                </div>
                                            </a>
                                        </div>
                                        <h4>Your Plan: Basic</h4>
                                        <p>
                                            There are total of 200 available accounts for this plan. There are 138 current active accounts and remaining 62 accounts to be generated.
                                        </p>
                                        <div>
                                            <span>Total generated accounts usage so far:</span>
                                            <div className="stat-percent">69%</div>
                                            <div className="progress progress-mini">
                                                <div style={{width: '69%'}} className="progress-bar"></div>
                                            </div>
                                        </div>
                                        <div className="row  m-t-sm">
                                            <div className="col-sm-4">
                                                <div className="font-bold">Current Package</div>
                                                200
                                            </div>
                                            <div className="col-sm-4">
                                                <div className="font-bold">Active Accounts</div>
                                                138
                                            </div>
                                            <div className="col-sm-4 text-right">
                                                <div className="font-bold">Available</div>
                                                62
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>



                            <div className="col-lg-8">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Accounts Table</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                    <i className="fa fa-chevron-up"></i>
                                                </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                    <i className="fa fa-wrench"></i>
                                                </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                    <i className="fa fa-times"></i>
                                                </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <div className="table-responsive">
                                            <table className="table table-striped table-bordered table-hover account-manage-table">
                                                <thead>
                                                    <tr>
                                                        <th>Profile</th>
                                                        <th>Name</th>
                                                        <th>Username</th>
                                                        <th>Unit No.</th>
                                                        <th>Email</th>
                                                        <th>Contact No.</th>
                                                        <th>View Account</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td className="client-avatar text-center">
                                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a1.jpg" /></a>
                                                        </td>
                                                        <td>
                                                            Aaron Kow
                                                        </td>
                                                        <td>
                                                            aaronkow2121
                                                        </td>
                                                        <td>
                                                            A-02-12
                                                        </td>
                                                        <td>
                                                            <i className="fa fa-envelope"></i> aaronkow@gmail.com
                                                        </td>
                                                        <td>
                                                            <i className="fa fa-phone"></i> 01829288311
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/account/view">view</Link>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td className="client-avatar text-center">
                                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a2.jpg" /></a>
                                                        </td>
                                                        <td>
                                                            Lisa Limbert
                                                        </td>
                                                        <td>
                                                            lisalimbert
                                                        </td>
                                                        <td>
                                                            A-12-13
                                                        </td>
                                                        <td>
                                                            <i className="fa fa-envelope"></i> lisalimbert@gmail.com
                                                        </td>
                                                        <td>
                                                            <i className="fa fa-phone"></i> 01831212122
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/account/view">view</Link>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td className="client-avatar text-center">
                                                            <a href="#"><img alt="member" className="img-circle" src="http://webapplayers.com/inspinia_admin-v2.7.1/img/a3.jpg" /></a>
                                                        </td>
                                                        <td>
                                                            Rosell Kurberg
                                                        </td>
                                                        <td>
                                                            rosellkurberg
                                                        </td>
                                                        <td>
                                                            A-03-08
                                                        </td>
                                                        <td>
                                                            <i className="fa fa-envelope"></i> rosellkurberg@gmail.com
                                                        </td>
                                                        <td>
                                                            <i className="fa fa-phone"></i> 01234567890
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/account/view">view</Link>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                {/*
                                                <tfoot>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </tfoot>
                                                */}
                                            </table>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(AccountManage);



